// STRUCTURE

html {
	background: LightSlateGray;
}





main {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: space-between;
	align-items: flex-start;

	padding: $var-padding;
	padding: var( --padding );

	background-color: GhostWhite;
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	background-attachment: fixed;



	&.center,
	&.Center {
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
	}



	@include breakpoint( portrait ) {
		background-position: 30% center;
	}



	@include breakpoint( mobile ) {
		flex-direction: column;
		justify-content: flex-start;
		align-items: center;
	}
}





header:not(.at-title):not(.at-markup):not(.HeaderHtml) {
	width: 14em;



	.left &,
	.Left & {
		order: 2;



		@include breakpoint( mobile ) {
			order: 1;
		}
	}

	.right &,
	.Right &,
	.center &
	.Center & {
		order: 1;
	}



	.center &,
	.Center & {
		margin-bottom: $var-padding;
		margin-bottom: var( --padding );
	}



	@include breakpoint( mobile ) {
		width: 55%;

		margin-bottom: $var-padding;
		margin-bottom: var( --padding );
	}
}





article {
	width: 50%;

	padding: calc( #{ $var-padding } * 0.75 );
	padding: calc( var( --padding ) * 0.75 );

	background: white;



	.left &,
	.Left & {
		order: 1;



		@include breakpoint( mobile ) {
			order: 2;
		}
	}

	.right &,
	.Right &,
	.center &
	.Center & {
		order: 2;
	}



	.center &,
	.Center & {
		width: 45em;
		max-width: 100%;
	}



	@include breakpoint( portrait ) {
		.left &,
		.Left &,
		.right &,
		.Right & {
			width: 65%;
		}
	}



	@include breakpoint( mobile ) {
		.left &,
		.Left &,
		.right &,
		.Right &,
		.center &,
		.Center & {
			width: 100%;
		}
	}
}





footer:not(.at-markup):not(.FooterHtml) {
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;

	padding: $var-padding;
	padding: var( --padding );

	background: LightSlateGray;
}

.footer__links {
	display: flex;
	flex-direction: row;
	flex-wrap: nowrap;
	justify-content: center;
	align-items: center;

	margin-bottom: calc( #{ $var-padding } * 0.75 );
	margin-bottom: calc( var( --padding ) * 0.75 );



	@include breakpoint( mobile ) {
		margin-bottom: $var-padding;
		margin-bottom: var( --padding );
	}
}