// Main

//// CSS Variables
@if($css4) {
	:root {
		@each $variable, $value in $css-variables {
			#{$variable}: $value;
		}
		

		
		@include breakpoint(large) {
			--padding: 4em;

			--fontsize: 1.15rem;
		}



		@include breakpoint(landscape) {
			--padding: 2.5em;

			--fontsize: 0.85rem;
		}

		@include breakpoint(portrait) {
			--padding: 2.25em;
		}



		@include breakpoint(mobile) {
			--padding: 1.75em;

			--fontsize: 0.8rem;
		}
		
		@include breakpoint(mobile-small) {
			--padding: 1.65em;
			
			--fontsize: 0.75rem;
		}
	}
}



* {
   	box-sizing: border-box;


   	&:before,
   	&:after {
   		box-sizing: border-box;
   	}
}



html {
	min-height: 100%;
	height: 100%;

	-webkit-text-size-adjust: none;
}



body {
	min-height: 100%;
	width: 100%;
	max-width: 100%;
	
	position: relative;

	font-size: $var-fontsize;
	font-size: var(--fontsize);
}



nav,
header,
article,
footer,
section,
div {
	display: block;
	position: relative;
}



main {
	position: relative;
	
	max-width: 100%;
	
	overflow-x: hidden;
}