// Buttons

a:focus,
button:focus,
div:focus {
	outline: none;
}



button {
	padding: 0;
	margin: 0;

	font-size: 1em;

	background: none;

	box-shadow: 0;

	border: 0;
	border-radius: 0;

	outline: none;
	
	appearance: none;

	cursor: pointer;
}





//// NGP Button
input.at-submit,
input.btn-at-primary {
	display: block !important;

	float: none !important;

	width: 100% !important;
	height: 65px !important;

	margin: 0 !important;

	background: gray !important;

	border: 0 !important;



	&:hover,
	&:active,
	&:focus {
		background: black !important;
	}
}