// Form Styles

input,
select {
	padding: 0;
	margin: 0;

	font-size: 1em;

	background: none;

	box-shadow: 0;

	border: 0;
	border-radius: 0;

	outline: none;
	
	appearance: none;
}

input:focus,
textarea:focus,
label:focus {
	outline: none !important;

	box-shadow: none !important;
}





//// NGP Form Overrides
.ngp-form {
	header,
	fieldset,
	div,
	legend,
	label,
	input,
	textarea,
	span,
	footer {
		padding: 0 !important;
		margin: 0 !important;
	}



	.at-title {
		display: none;
	}



	.at-row {
		width: 100% !important;

		display: flex !important;
		flex-direction: row !important;
		flex-wrap: nowrap !important;
		justify-content: space-between !important;
		align-items: flex-start !important;



		label {
			flex: 0 0 0 !important;

			width: calc( ( 100% - 10px ) / 2 ) !important;
			min-width: calc( ( 100% - 10px ) / 2 ) !important;
			max-width: 100% !important;



			@include has-exactly( 3 ) {
				width: calc( ( 100% - 20px ) / 3 ) !important;
				min-width: calc( ( 100% - 20px ) / 3 ) !important;
			}
		}



		&.at-row-solo,
		&.at-row-full {
			label {
				flex: 1 0 0 !important;

				width: 100% !important;



				@include has-exactly( 3 ) {
					width: 100% !important;
				}
			}
		}



		&.other-option {
			flex-wrap: wrap !important;



			label {
				min-width: 100% !important;
				width: 100% !important;
			}



			.at-text {
				padding-left: calc( 30px + 1em ) !important;
			}
		}



		@include breakpoint( mobile ) {
			flex-wrap: wrap !important;



			label {
				flex: 1 0 0 !important;

				min-width: 100% !important;
				width: 100% !important;
			}
		}
	}



	.HeaderHtml,
	fieldset {
		margin-bottom: calc( #{ $var-padding } * 0.5 ) !important;
		margin-bottom: calc( var( --padding ) * 0.5 ) !important;
	}



	legend {
		margin-bottom: 5px !important;
	}

	label {
		margin-bottom: 10px !important;



		&.at-check {
			margin-bottom: 5px !important;
		}
	}

	.radios {
		label {
			display: flex !important;
			flex-direction: row !important;
			flex-wrap: nowrap !important;
			justify-content: flex-start !important;
			align-items: center !important;

			margin-bottom: 5px !important;
		}
	}



	.at-row-full {
		.at-area,
		.at-text {
			margin-bottom: 20px !important;
		}
	}



	input,
	textarea,
	select,
	.allow-dropdown {
		margin-top: 5px !important;

		height: 50px !important;

		padding: 0 15px !important;

		border: 1px solid black !important;
		border-radius: 0 !important;



		&:hover {
			border-color: gray !important;
		}



		input {
			height: 100% !important;

			padding-left: 45px !important;
			padding-right: 15px !important;

			border-width: 0 !important;
		}



		.flag-container {
			padding-left: 10px !important;
		}
	}

	.allow-dropdown {
		padding: 0 !important;
	}

	textarea {
		height: 200px !important;

		padding: 15px !important;

		resize: none !important;
	}



	.at-checkbox-title-container {
		display: flex !important;
		flex-direction: row !important;
		flex-wrap: nowrap !important;
		justify-content: flex-start !important;
		align-items: center !important;

		flex-grow: 1 !important;

		float: none !important;



		.at-checkbox-title {
			flex-grow: 1 !important;
		}



		&:before {
			position: relative !important;
			top: auto !important;

			flex-shrink: 0 !important;

			width: 30px !important;
			height: 30px !important;

			margin-right: 1em !important;

			background: none !important;

			border: 1px solid black !important;
			border-radius: 0 !important;

			box-shadow: none !important;
		}

		&:after {
			top: 15px !important;
			left: 15px !important;

			font-size: 1.25em !important;

			color: black !important;

			transform: translate( -50%, -50% ) !important;
		}



		&:hover {
			&:before {
				border-color: gray !important;
			}
		}
	}



	input[type=radio] {
		flex-shrink: 0 !important;
		
		position: relative !important;

		width: 30px !important;
		height: 30px !important;

		margin-top: 0 !important;
		margin-right: 1em !important;
		padding: 0 !important;

		border-radius: 100% !important;

		cursor: pointer !important;



		&:before {
			position: absolute;
			top: 50%;
			left: 50%;

			width: 20px;
			height: 20px;

			background: black;

			border-radius: 100%;

			transform: translate( -50%, -50% );

			opacity: 0;

			content: '';
		}



		&:checked {
			&:before {
				opacity: 1;
			}
		}
	}
}



.country-list {
	margin-left: -2px !important;
	margin-top: -1px !important;

	border: 1px solid black !important;

	box-shadow: none !important;
}



.FastAction {
	padding: 0 !important;



	&.at-markup {
		margin-bottom: calc( #{ $var-padding } * 0.5 ) !important;
		margin-bottom: calc( var( --padding ) * 0.5 ) !important;
	}



	.fastAction.clearfix p {
		display: flex !important;
		flex-direction: row !important;
		flex-wrap: nowrap !important;
		justify-content: space-between !important;
		align-items: flex-start !important;



		.fa-cta {
			order: 2;

			flex-shrink: 0 !important;

			padding-left: 1em !important;
		}

		.fa-lead {
			order: 1;
		}
	}



	.circle {
		background: gray !important;



		&:hover,
		&:active,
		&:focus {
			background: black !important;
		}
	}
}

.UpdateMyProfile {
	padding: 0 !important;
	padding-top: 10px !important;
	margin: 0 !important;
}

.updateMyProfileSection {
	label {
		display: flex !important;
		flex-direction: row !important;
		flex-wrap: nowrap !important;
		justify-content: flex-start !important;
		align-items: center !important;

		margin-bottom: 0 !important;
		margin-top: 10px !important;

		cursor: pointer !important;



		.text {
			flex-grow: 1 !important;
		}



		& > span {
			display: flex !important;
			flex-direction: row !important;
			flex-wrap: nowrap !important;
			justify-content: flex-start !important;
			align-items: center !important;
		}



		input[type="checkbox"] + span {
			&:before {
				position: relative !important;
				top: auto !important;

				flex-shrink: 0 !important;

				width: 30px !important;
				height: 30px !important;

				margin-right: 1em !important;

				background: none !important;

				border: 1px solid black !important;
				border-radius: 0 !important;

				box-shadow: none !important;

				content: '';
			}



			&:after {
				position: absolute !important;
				
				content: "âœ”" !important;
				font-family: 'Arial Unicode MS' !important;
				line-height: 1 !important;

				top: 15px !important;
				left: 15px !important;

				font-size: 1.25em !important;

				color: black !important;

				transform: translate( -50%, -50% ) !important;
				transition: all 0.1s ease-in-out !important;

				opacity: 0 !important;
			}
		}

		input[type="checkbox"]:checked + span {
			&:after {
				opacity: 1 !important;
			}
		}
	}
}